* {
  padding: 0;
  margin: 0; }

.is-hidden {
  display: none !important; }

body {
  font-size: 16px;
  font-family: 'Roboto', sans-serif; }

.theme {
  background: #282a36;
  color: #f8f8f2;
  padding: 15px 15px 0; }

.divider {
  background-image: url("divider.svg");
  width: 110px;
  height: 45px;
  background-position: center;
  margin: 0 auto; }

.resume {
  max-width: 900px;
  min-width: 360px;
  margin: 0 auto; }
  .resume__header {
    display: flex;
    flex-direction: column;
    margin-top: 15px; }
    .resume__header-title {
      font-family: 'BioRhyme Expanded', serif;
      display: flex;
      flex-direction: column;
      text-align: center;
      font-size: 2rem;
      margin-top: 15px; }
      .resume__header-title span[class*=group]:not(:last-of-type) {
        margin-right: 15px; }
      .resume__header-title .group-0 {
        color: #50fa7b; }
      .resume__header-title .group-1 {
        color: #50fa7b; }
      .resume__header-title .group-2 {
        color: #ffb86c;
        margin-bottom: 15px; }
        @media (min-width: 767px) {
          .resume__header-title .group-2 {
            margin-bottom: 0; } }
      @media (min-width: 767px) {
        .resume__header-title {
          text-align: left;
          margin-top: 30px; } }
    .resume__header-button {
      padding: 20px;
      width: 90%;
      display: block;
      margin: 0 auto;
      background: #ff3c3c;
      color: #f8f8f2;
      border: inset 1px rgba(248, 248, 242, 0.7);
      border-radius: 4px;
      font-size: 1.5rem;
      font-weight: 300;
      text-shadow: -1px 1px 1px #282a36; }
      .resume__header-button:hover {
        cursor: pointer;
        background: #ff2222; }
      .resume__header-button:focus {
        outline: #ff5555; }
      .resume__header-button:active {
        background: #d50000;
        border: 0; }
      @media (min-width: 767px) {
        .resume__header-button {
          width: 100%; } }
  .resume__content {
    padding: 30px 15px 0; }
    @media (min-width: 767px) {
      .resume__content {
        padding-left: 0;
        padding-right: 0; } }
  .resume__aside {
    padding-bottom: 15px; }
    @media (min-width: 767px) {
      .resume__aside {
        display: flex;
        justify-content: space-between; } }
    .resume__aside-nav:first-of-type {
      margin-bottom: 15px; }
      @media (min-width: 767px) {
        .resume__aside-nav:first-of-type {
          margin-bottom: 0; } }
    .resume__aside-title {
      font-size: 1.2rem;
      margin-bottom: 5px; }
    .resume__aside-list {
      list-style: none; }
      .resume__aside-list.dev-icons li {
        display: flex;
        align-items: center;
        line-height: 25px;
        padding: 5px 0; }
        .resume__aside-list.dev-icons li:nth-child(1)::before {
          content: url(github.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
        .resume__aside-list.dev-icons li:nth-child(2)::before {
          content: url(npm.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
        .resume__aside-list.dev-icons li:nth-child(3)::before {
          content: url(web.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
        .resume__aside-list.dev-icons li:nth-child(4)::before {
          content: url(linkedin.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
      .resume__aside-list.me-icons li {
        display: flex;
        align-items: center;
        line-height: 25px;
        padding: 5px 0; }
        .resume__aside-list.me-icons li:nth-child(1)::before {
          content: url(me.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
        .resume__aside-list.me-icons li:nth-child(2)::before {
          content: url(email.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
        .resume__aside-list.me-icons li:nth-child(3)::before {
          content: url(telephone.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
        .resume__aside-list.me-icons li:nth-child(4)::before {
          content: url(address.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
  .resume__main {
    padding-top: 15px; }
    .resume__main-title {
      margin-bottom: 15px; }
    .resume__main-list li {
      display: flex;
      align-items: center;
      line-height: 30px;
      padding: 5px 0; }
      .resume__main-list li::before {
        content: url(robot.svg);
        width: 30px;
        height: 30px;
        margin-right: 10px; }
  .resume__summary {
    padding-bottom: 10px; }
    .resume__summary p {
      margin-bottom: 10px;
      line-height: 25px; }
      .resume__summary p:last-of-type {
        margin-bottom: 0; }
  .resume__experience {
    padding-top: 10px; }
    .resume__experience-item {
      margin-bottom: 20px; }
      .resume__experience-item .resume__main-title {
        margin-bottom: 10px;
        font-weight: 100; }
        .resume__experience-item .resume__main-title:first-child {
          margin-bottom: 5px; }
      .resume__experience-item:nth-of-type(2) .resume__main-list li {
        display: flex;
        align-items: center;
        line-height: 30px;
        padding: 5px 0; }
        .resume__experience-item:nth-of-type(2) .resume__main-list li::before {
          content: url(lamp.svg);
          width: 30px;
          height: 30px;
          margin-right: 10px; }
      .resume__experience-item:nth-of-type(3) .resume__main-list li {
        display: flex;
        align-items: center;
        line-height: 25px;
        padding: 5px 0; }
        .resume__experience-item:nth-of-type(3) .resume__main-list li::before {
          content: url(atom.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
      .resume__experience-item:last-of-type {
        margin-bottom: 15px; }
  .resume__education {
    padding-top: 10px; }
    .resume__education-item {
      margin-bottom: 15px; }
      .resume__education-item .resume__main-list li {
        display: flex;
        align-items: center;
        line-height: 25px;
        padding: 5px 0; }
        .resume__education-item .resume__main-list li::before {
          content: url(code.svg);
          width: 25px;
          height: 25px;
          margin-right: 10px; }
      .resume__education-item .resume__main-list span {
        display: flex;
        flex-direction: column; }
  .resume__skills {
    margin: 0 auto; }
    .resume__skills tbody {
      display: flex;
      flex-direction: column; }
    .resume__skills tr {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-bottom: 5px; }
    .resume__skills td {
      padding: 10px 15px;
      box-shadow: 0 0 3px #ff5555;
      flex-grow: 1;
      margin: 5px; }
  .resume__footer {
    width: 100%;
    height: 100px;
    background-image: url(footer.svg);
    background-size: contain;
    margin-top: 30px;
    position: absolute;
    left: 0; }
    .resume__footer-copy {
      text-align: center;
      word-spacing: 5px;
      font-size: 1.2rem;
      width: 95%;
      margin: 0 auto;
      padding: 7px 5px 3px;
      background: #282a36;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 25px 25px 0 0; }
      @media (min-width: 767px) {
        .resume__footer-copy {
          width: 50%; } }
      .resume__footer-copy span {
        display: inline-block;
        transform: rotate(270deg);
        color: #ff79c6;
        font-family: monospace;
        font-size: 1.5rem;
        font-weight: bold;
        letter-spacing: -4px;
        text-shadow: 0px 2px 1px #f8f8f2; }
